<template>
  <div class="nav navbar">
    <div class="logo-container">
      <h4>Covid Tracker</h4>
    </div>
    <div class="nav-container">
      <ul class="nav-links d-inline">
        <li>
          <a href="" class="anchor"><i class="fas fa-virus danger"></i> Enfekte</a>
          <h6 class="confirmed">{{ totalConfirmed }}</h6>
        </li>
        <li>
          <a href="" class="anchor"
            ><i class="fas fa-skull-crossbones danger"></i> Ölümler</a
          >
          <h6 class="death">{{ totalDeaths }}</h6>
        </li>
        <li>
          <a href="" class="recovered anchor"
            ><i class="fas fa-shield-alt recovered"></i> İyileşenler</a
          >
          <h6 class="recovered">{{ totalRecovered }}</h6>
        </li>
        <li>
          <a href="" class="anchor">
            <i class="fas fa-globe-americas"></i>Total</a
          >
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import API from "../services/API";

export default {
  data() {
    return {
      totalConfirmed: "",
      totalDeaths: "",
      totalRecovered: "",
    };
  },

  methods: {
    kFormatter(num) {
      return Math.abs(num) > 999
        ? Math.sign(num) * (Math.abs(num) / 1000000).toFixed(1) + "M"
        : Math.sign(num) * Math.abs(num);
    },
  },

  async created() {
    try {
      let res = await API().get("/world/total");
      this.totalConfirmed = this.kFormatter(res.data.TotalConfirmed);
      this.totalDeaths = this.kFormatter(res.data.TotalDeaths);
      this.totalRecovered = this.kFormatter(res.data.TotalRecovered);
      console.log(res)
    } catch (error) {
      return error;
    }
  },
};
</script>

<style lang="scss" scoped>
.nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-wrap: wrap;
  background: #343a44;
}
.logo-container {
  flex: 1;
  h4 {
    color: #f2f2f2;
    font-size: 2rem;
  }
}
.nav-container {
  flex: 2;

  h6 {
    color: #db3c32;
    font-size: 1.5rem;
  }
  .nav-links {
    display: flex;
    justify-content: space-around;
    list-style: none;

    .anchor {
      //  border: 3px solid #3d3f4c;
      padding: 0.2rem;
      margin-right: 3.9rem;
      font-size: 2.3rem;
      color: lightblue;
      text-decoration: none;
      background-color: transparent;
      border-radius: 10px;
      transition: all 0.7s;
      &:hover {
        background: rgba(185, 195, 196, 0.2);
      }
    }
  }

  .recovered {
    color: rgb(85, 185, 85);
  }

  .danger{
      color: rgb(168, 66, 66);
  }
}

@media screen and (max-width: 768px) {
  .nav-links {
    flex-direction: column;
  }
}
