      <p v-if="country.Date" class="date">{{ new Date(country.Date).getDate() }} Aralık</p>
    </div>
    <div class="details">
      <p class="en" v-if="country.TotalConfirmed > 0">
         <span>Enfekte</span> : {{ country.TotalConfirmed }}
      </p>
      <p class="en" v-if="country.Confirmed > 0"> <span>Enfekte</span> : {{ country.Confirmed }}</p>
      <p class="en" v-if="country.TotalDeaths > 0"> <span>Ölüm</span> : {{ country.TotalDeaths }}</p>
      <p class="en" v-if="country.Deaths > 0"> <span>Ölüm</span> : {{ country.Deaths }}</p>
      <p class="en" v-if="country.TotalRecovered > 0">
