      </p>
      <p class="en" v-if="country.Recovered > 0"> <span>İyileşen</span> : {{ country.Recovered }}</p>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    country: { type: Object },
  },
};
</script>

