<template>
  <div class="country">
    <input type="text" placeholder="Search" @keyup="searchText($event)" />
    <hr />
    <app-country-list
      v-for="country in allCountry"
      :key="country.index"
      :country="country"
    ></app-country-list>
    <app-country-list :country="country"></app-country-list>
  </div>
</template>

<script>
import CountryList from "./CountryList";
import API from "../services/API";
export default {
  data() {
    return {
      country: {},
      allCountry: [],
    };
  },
  methods: {
    async searchText(e) {
      let date = new Date();
      let yesterday = new Date();
      yesterday.setDate(date.getDate() - 1);
      yesterday.setHours(0, 0, 0, 0);
      date.setHours(0, 0, 0, 0);
      try {
        if (e.target.value != "") {
          let res = await API().get(
            `/country/${
              e.target.value
            }?from=${yesterday.toISOString()}&to=${date.toISOString()}`
          );
          if (res.data.Message) {
            this.allCountry.push({ message: res.data.Message });
          }

          if (res.data) {
            console.log(res.data[0]);
            this.country = res.data[0];
            this.allCountry = [];
          }
        } else {
          this.country = {};
          this.allCountry = [];
          await this.getAll();
        }
      } catch (error) {
        return error;
      }
    },

    async getAll() {
      try {
        let res = await API().get("/summary");
        if (res.data.Countries) {
          res.data.Countries.forEach((element) => {
            if (this.allCountry.length > 20) {
              return;
            }
            return this.allCountry.push(element);
          });
        } else {
          this.allCountry.push({ message: res.data.Message });
        }
      } catch (error) {
        console.log(error);
      }
    },
  },

  components: {
    appCountryList: CountryList,
  },

  async created() {
    await this.getAll();
  },
};
</script>

<style lang="scss" scoped>
.country {
  background-color: #343a44;
  width: 20rem;
  height: auto;
  text-align: center;
  overflow-y: scroll;
  input {
    width: 180px;
    height: 30px;
    border: none;
    outline: none;
    border-radius: 10px;
  }
}
</style>

