<template>
  <div class="container">
    <div class="news-section">
        <app-news v-for="newss in news" :key="newss.index" :newss="newss"></app-news>
    </div>
    <app-country></app-country>
  </div>
</template>

<script>
import Country from "./Country";
import News from "./News"
import NewService from "../services/NewsService";
export default {
  data() {
    return {
      news: [],
    };
  },
  components: {
    appCountry: Country,
    appNews:News
  },

  async created() {
    let sonuc = await NewService.getNews();
    this.news = sonuc;
  },
};
</script>

<style lang="scss" scoped>
.news-section {
  width: 100%;
}

.container {
  display: flex;
  justify-content: flex-end;
}
@media screen and(max-width: 768px) {
  .container {
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
  }
}
</style>>
