import axios from "axios";
import jsdom from "jsdom"

const { JSDOM } = jsdom;

class NewsService {
    static async getNews() {
        const response = await axios.get("https://www.hurriyet.com.tr/corona-virusu/");
        let data = [];
        let dom = new JSDOM(response.data);
        let news = dom.window.document.querySelectorAll('.box-title')

        news.forEach(item => {
            if (data.length > 10) {
                return
            }
            let boxMedia = dom.window.document.querySelectorAll('.box-media-img');
            boxMedia.forEach(imageItem => {

                if (imageItem.getAttribute("title") == item.getAttribute("title")) {
                    data.push({
                        title: item.getAttribute("title"),
                        link: "https://hurriyet.com.tr" + item.parentElement.parentElement.getAttribute("href"),
                        image: imageItem.getAttribute("src"),
                    });
                }
            })



        })

        return data;
    }

}


export default NewsService;